<template>
  <div class="box">
    <div class="right-box">
      <div class="content-box">
        <label>公司名字: </label>
        <input type="text" v-model="name">
      </div>

      <div class="content-box">
        <label>公司税号: </label>
        <input type="text" v-model="tax">
      </div>

      <div class="content-box">
        <label>邮箱账号: </label>
        <input type="text" v-model="email">
      </div>

      <div class="content-box">
        <label>公司地址: </label>
        <input type="text" v-model="address" placeholder="默认不填">
      </div>

      <div class="content-box">
        <label>收据描述: </label>
        <input type="text" v-model="description">
      </div>

      <div class="content-box">
        <label>收据金额: </label>
        <input type="number" v-model="money" placeholder="只可以填纯数字">
      </div>

      <div class="content-box">
        <button @click="reloadPage">刷新</button>
        <button @click="drawNew">生成</button>
        <button @click="taxSwitchF">是否含税? = <span style="color:red" :style="{ color: taxSwitch ? 'green' : 'red' }">{{
          taxSwitch }}</span></button>
      </div>
      
      <div class="content-box">
        <button @click="exportToPDF">导出为PDF</button>
        <button @click="exportToImg">导出为图片</button>
      </div>
    </div>
    <canvas ref="canvas"></canvas>
  </div>
</template>

<script>
import jsPDF from 'jspdf';

export default {
  data() {
    return {
      name: '',
      email: '',
      address: '',
      tax: '',
      description: 'Network Services',
      money: null,
      imageLoaded: false,
      taxSwitch: true,
      textColor: '#000000',
      fapiao: require('@/assets/FAPIAO2.png'),
      initialCanvasImage: null, // 新增变量用于保存初始画布状态
    };
  },
  mounted() {
    this.loadImage();
  },
  methods: {
    taxSwitchF() {
      this.taxSwitch = !this.taxSwitch
    },
    reloadPage() {
      if (this.initialCanvasImage) {
        const canvas = this.$refs.canvas;
        const ctx = canvas.getContext('2d');
        ctx.clearRect(0, 0, canvas.width, canvas.height); // 清除画布
        ctx.drawImage(this.initialCanvasImage, 0, 0); // 使用保存的画布状态重置画布
      }
    },
    loadImage() {
      const img = new Image();
      img.onload = () => {
        this.setupCanvas(img);
        this.adjustImage();
      };
      img.src = this.fapiao;
    },
    setupCanvas(img) {
      const canvas = this.$refs.canvas;
      const ctx = canvas.getContext('2d');
      canvas.width = img.width;
      canvas.height = img.height;
      ctx.drawImage(img, 0, 0);
      this.imageLoaded = true;
      // 保存初始画布状态
      this.initialCanvasImage = new Image();
      this.initialCanvasImage.src = canvas.toDataURL();
    },
    adjustImage() {
      const canvas = this.$refs.canvas;
      const ctx = canvas.getContext('2d');
      let imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
      imageData = this.applyContrast(imageData, 30); // 30的对比度提升
      ctx.putImageData(imageData, 0, 0);
      this.calculateTextColor(imageData);
    },
    calculateTextColor(imageData) {
      let r = 0, g = 0, b = 0, total = 0;
      const data = imageData.data;
      for (let i = 0; i < data.length; i += 4) {
        r += data[i];
        g += data[i + 1];
        b += data[i + 2];
        total++;
      }
      r /= total;
      g /= total;
      b /= total;
      const brightness = Math.sqrt(0.299 * r * r + 0.587 * g * g + 0.114 * b * b);
      this.textColor = brightness > 128 ? '#000000' : '#FFFFFF';
    },
    applyContrast(data, contrast) {
      const factor = (259 * (contrast + 255)) / (255 * (259 - contrast));
      for (let i = 0; i < data.data.length; i += 4) {
        data.data[i] = factor * (data.data[i] - 128) + 128;
        data.data[i + 1] = factor * (data.data[i + 1] - 128) + 128;
        data.data[i + 2] = factor * (data.data[i + 2] - 128) + 128;
      }
      return data;
    },
    drawText(temp, x, y, font, tt, fromRight = false) {
      if (!this.imageLoaded) return;
      const canvas = this.$refs.canvas;
      const ctx = canvas.getContext('2d');
      ctx.drawImage(canvas, 0, 0);
      ctx.font = font;
      ctx.fillStyle = "#030030";
      ctx.textAlign = fromRight ? 'right' : 'left';



      if (tt == 1) {
        ctx.fillText(temp, x, y);
      } else if (/[a-zA-Z]/.test(this.name)) {
        // 英文名公司字间距
        let letterSpacing = 0;
        let startX = x;
        let startY = y;

        for (let i = 0; i < temp.length; i++) {
          const char = temp[i];
          ctx.fillText(char, startX, startY);
          const metrics = ctx.measureText(char);
          startX += metrics.width + letterSpacing;
        }
      } else {
        let letterSpacing = 0;
        let startX = x;
        let startY = y;

        for (let i = 0; i < temp.length; i++) {
          const char = temp[i];
          ctx.fillText(char, startX, startY);
          const metrics = ctx.measureText(char);
          startX += metrics.width + letterSpacing;
        }
      }
    },
    getTodayDate(temp) {
      let date = new Date();
      date.setDate(date.getDate() + temp);
      let day = date.getDate().toString().padStart(2, '0');
      let month = (date.getMonth() + 1).toString().padStart(2, '0');
      let year = date.getFullYear();
      return `${day}/${month}/${year}`;
    },
    getRandomNumber() {
      let random = Math.floor(Math.random() * (1000 - 100) + 100)
      return random;
    },
    drawDate() {
      // ======= 日期 =======
      // Tax #
      this.drawText("Tax", 530, 124,'300 17px Agency FB', true)
      this.drawText("Tax", 530, 125,'500 17px Agency FB', true)
      this.drawText(this.tax, 530, 145,'500 15px Agency FB', true)
      // INVOICE #
      this.drawText("US-" + this.getRandomNumber(), 625, 210, '500 17px Agency FB', true, true)
      // INVOICE DATE
      this.drawText(this.getTodayDate(0), 625, 232, '500 17px Agency FB', true, true)
      // P.O.#
      this.drawText('2312/2024', 625, 254, '500 17px Agency FB', true, true)
      // DUE DATE
      this.drawText(this.getTodayDate(15), 625, 276, '500 17px Agency FB', true, true)
    },
    drawMoreEmail() {
      // ======= 邮箱 =======
      this.drawText(this.email, 256, 263, '16px Agency FB', true)
    },
    drawMoreAmount() {
      // ======= 金额 =======
      // Unit Price
      this.drawText(this.money, 400, 377, '300 20px Agency FB', true)
      // Description
      this.drawText(this.description, 245, 377, '300 20px Agency FB', true)
      // Amount
      this.drawText(this.money, 580, 377, '20px Agency FB', true)
    },
    drawMoreTax() {
      // Tax
      if (this.taxSwitch) {
        this.drawText((this.money * 0.0625).toFixed(2), 580, 414, '20px Agency FB', true)
      } else {
        this.drawText((0.0).toFixed(2), 580, 414, '20px Agency FB', true)
      }
    },
    drawMoreTotal() {
      // Total
      if (this.taxSwitch) {
        this.drawText("¥ " + (this.money * 0.0625 + this.money).toFixed(2), 560, 450, '900 20px Agency FB', true)
      } else {
        this.drawText("¥ " + (this.money * 0.0 + this.money).toFixed(2), 560, 450, '900 20px Agency FB', true)
      }
    },
    drawMoreCompany() {

      // ======= 公司名称 =======
      if (!/[a-zA-Z]/.test(this.name)) {
        // Bill to
        this.drawText(this.name, 253, 145, '16px STZhongsong', false)
        // company
        if (this.name.length < 10) {
          this.drawText(this.name, 50, 377, '18px STZhongsong', false)
        } else {
          this.drawText(this.name.slice(0, 8), 50, 377, '18px STZhongsong', false)
          this.drawText(this.name.slice(8), 50, 399, '18px STZhongsong', false)
        }

      } else {
        // Bill to
        this.drawText(this.name, 253, 145, '16px Agency FB', false)
        // company
        let arr = this.name.split(" ")
        let firstText = arr[0] + " " + arr[1]
        this.drawText(firstText, 50, 377, '18px Agency FB', false)
        this.drawText(firstText, 50, 377, '18px Agency FB', false)

        let secondText = ""
        for (let i = 2; i < arr.length; i++) {
          secondText = secondText + arr[i] + " "
          console.log(secondText)
        }
        this.drawText(secondText, 50, 399, '18px Agency FB', false)
        this.drawText(secondText, 50, 399, '18px Agency FB', false)
      }
    },
    drawNew() {
      this.drawDate()
      this.drawMoreTotal()
      this.drawMoreCompany()

      // 重影
      for (let i = 0; i < 2; i++) {
        this.drawMoreEmail()
        this.drawMoreAmount()
        this.drawMoreTax()
      }
    },
    exportToImg() {
      // 获取已经绘制完成的 canvas 元素
      const canvas = this.$refs.canvas;
      // 将 canvas 转换为图像数据（这里用 PNG 格式）
      const imageData = canvas.toDataURL('image/png');
      // 创建一个临时的下载链接
      const link = document.createElement('a');
      link.href = imageData;
      link.download = 'canvas-image.png'; // 文件名及后缀
      link.click(); // 触发点击，自动下载图片
    },
    async exportToPDF() {
      // 获取已经绘制完成的 canvas 元素
      const canvas = this.$refs.canvas;
      // 将 canvas 转换为图像数据
      const imageData = canvas.toDataURL('image/jpeg');
      // 创建 PDF 实例，并设置格式和单位
      const pdf = new jsPDF({
        orientation: 'portrait', // 纵向
        unit: 'px', // 使用像素单位
        format: [canvas.width, canvas.height], // PDF 大小与 canvas 一致
      });

      // 将图像数据添加到 PDF 中
      pdf.addImage(imageData, 'JPEG', 0, 0, canvas.width, canvas.height);
  
      // 保存 PDF 文件
      pdf.save('image.pdf'); // 下载 PDF 文件
    }
  }
};
</script>

<style>
canvas {
  height: 90vh;
  display: block;
}

.notice span {
  color: darkred;
  font-weight: 700;
  padding-right: 10px;
}

.notice p {
  font-size: small;
  margin: 10px;
}

.box {
  display: flex;
  flex-direction: row;
}

.content-box {
  margin: 20px 0;
}

.content-box button {
  margin: 0 10px;
}
</style>
